.root {
  display: block;
  top: 0;
  position: absolute;
  width: 100%;
  height: 100%;
}
/**
 HEADER
 */
.header {
  position: relative;
}
.logo {
  position: absolute;
  top: auto;
  left: 50%;
  transform: translateX(-50%);
  margin-top: 26.66666667vw;
}
@media (min-aspect-ratio: 375 / 668) {
  .logo {
    margin-top: calc( (100 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .logo {
    margin-top: calc( (100 / 900) * (var(--vh, 1vh) * 100) );
  }
}
.githubLink {
  position: absolute;
  top: 8vw;
  right: 8vw;
}
@media (min-aspect-ratio: 375 / 668) {
  .githubLink {
    top: calc( (30 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .githubLink {
    top: calc( (30 / 900) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-aspect-ratio: 375 / 668) {
  .githubLink {
    right: calc( (30 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .githubLink {
    right: calc( (30 / 900) * (var(--vh, 1vh) * 100) );
  }
}
.github {
  transform: scale(1);
  transition: transform 200ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
}
.github path {
  transition: fill 200ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
}
.github:hover {
  transform: scale(1.2);
}
.github:hover path {
  fill: #fe3457;
}
/**
CONTENT
 */
.content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  margin-top: 13.33333333vw;
}
@media (min-aspect-ratio: 375 / 668) {
  .content {
    margin-top: calc( (50 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .content {
    margin-top: calc( (50 / 900) * (var(--vh, 1vh) * 100) );
  }
}
.line {
  background-color: #283e63;
  width: 100vw;
  height: 20vw;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin-top: 0vw;
}
@media (min-aspect-ratio: 375 / 668) {
  .line {
    height: calc( (75 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .line {
    height: calc( (100 / 900) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-aspect-ratio: 375 / 668) {
  .line {
    margin-top: calc( (0 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .line {
    margin-top: calc( (0 / 900) * (var(--vh, 1vh) * 100) );
  }
}
.polaroids {
  width: 100%;
  overflow: hidden;
  height: 120vw;
  display: block;
  transform-origin: center center;
}
@media (min-aspect-ratio: 375 / 668) {
  .polaroids {
    height: calc( (450 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .polaroids {
    height: calc( (450 / 900) * (var(--vh, 1vh) * 100) );
  }
}
.polaroidsInner {
  width: 100%;
  height: 100%;
  transform: scale(0.66);
}
@media (min-width: 768px) {
  .polaroidsInner {
    transform: none;
  }
}
.polaroid {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.polaroid_left {
  margin-left: -29.33333333vw;
  transform: translate(-50%, -50%) scale(0.85) rotate(-17deg);
}
@media (min-aspect-ratio: 375 / 668) {
  .polaroid_left {
    margin-left: calc( (-110 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .polaroid_left {
    margin-left: calc( (-110 / 900) * (var(--vh, 1vh) * 100) );
  }
}
.polaroid_center {
  transform: translate(-50%, -50%) scale(0.6) rotate(2deg);
  margin-top: -29.33333333vw;
  margin-left: 16vw;
}
@media (min-aspect-ratio: 375 / 668) {
  .polaroid_center {
    margin-top: calc( (-110 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .polaroid_center {
    margin-top: calc( (-110 / 900) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-aspect-ratio: 375 / 668) {
  .polaroid_center {
    margin-left: calc( (60 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .polaroid_center {
    margin-left: calc( (60 / 900) * (var(--vh, 1vh) * 100) );
  }
}
.polaroid_right {
  margin-left: 29.33333333vw;
  transform: translate(-50%, -50%) scale(0.67) rotate(13deg);
}
@media (min-aspect-ratio: 375 / 668) {
  .polaroid_right {
    margin-left: calc( (110 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .polaroid_right {
    margin-left: calc( (110 / 900) * (var(--vh, 1vh) * 100) );
  }
}
.input {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  position: fixed;
  top: auto;
  bottom: 18.66666667vw;
}
@media (min-aspect-ratio: 375 / 668) {
  .input {
    bottom: calc( (70 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .input {
    bottom: calc( (75 / 900) * (var(--vh, 1vh) * 100) );
  }
}
/**
 FOOTER
 */
.footer {
  width: 100%;
  text-align: center;
  position: absolute;
  bottom: 8vw;
}
@media (min-aspect-ratio: 375 / 668) {
  .footer {
    bottom: calc( (30 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .footer {
    bottom: calc( (0 / 900) * (var(--vh, 1vh) * 100) );
  }
}
.donateLink {
  visibility: hidden;
  display: block;
  font-size: 3.46666667vw;
  color: #fff;
  transform: scale(1);
  transition: all 200ms ease-out;
}
@media (min-aspect-ratio: 375 / 668) {
  .donateLink {
    font-size: calc( (13 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .donateLink {
    font-size: calc( (17 / 900) * (var(--vh, 1vh) * 100) );
  }
}
.donateLink:hover {
  color: #fe3457;
}
@media (min-width: 768px) {
  .donateLink:hover {
    transform: scale(1.05);
  }
}
@media (min-width: 768px) {
  .donateLink {
    position: absolute;
    left: 8vw;
    bottom: 8vw;
  }
}
@media (min-width: 768px) and (min-aspect-ratio: 375 / 668) {
  .donateLink {
    left: calc( (30 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) and (min-width: 768px) {
  .donateLink {
    left: calc( (30 / 900) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) and (min-aspect-ratio: 375 / 668) {
  .donateLink {
    bottom: calc( (30 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) and (min-width: 768px) {
  .donateLink {
    bottom: calc( (30 / 900) * (var(--vh, 1vh) * 100) );
  }
}
.donateLink span {
  text-decoration: underline;
}
.creditLink {
  visibility: hidden;
  display: block;
  margin-top: 2.66666667vw;
  font-size: 3.46666667vw;
  color: #fff;
  transform: scale(1);
  transition: all 200ms ease-out;
}
@media (min-aspect-ratio: 375 / 668) {
  .creditLink {
    margin-top: calc( (10 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .creditLink {
    margin-top: calc( (0 / 900) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-aspect-ratio: 375 / 668) {
  .creditLink {
    font-size: calc( (13 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .creditLink {
    font-size: calc( (17 / 900) * (var(--vh, 1vh) * 100) );
  }
}
.creditLink:hover {
  color: #fe3457;
}
@media (min-width: 768px) {
  .creditLink:hover {
    transform: scale(1.05);
  }
}
@media (min-width: 768px) {
  .creditLink {
    position: absolute;
    right: 8vw;
    bottom: 8vw;
  }
}
@media (min-width: 768px) and (min-aspect-ratio: 375 / 668) {
  .creditLink {
    right: calc( (30 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) and (min-width: 768px) {
  .creditLink {
    right: calc( (30 / 900) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) and (min-aspect-ratio: 375 / 668) {
  .creditLink {
    bottom: calc( (30 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) and (min-width: 768px) {
  .creditLink {
    bottom: calc( (30 / 900) * (var(--vh, 1vh) * 100) );
  }
}
