.root {
  position: absolute;
  top: 0;
  left: 0;
}
.rect {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(12px);
  border: 1px solid #fe3457;
}
.cross {
  width: 6.13333333vw;
  height: 6.13333333vw;
  display: block;
  position: absolute;
  top: -3.06666667vw;
  right: -3.06666667vw;
  z-index: 0;
  cursor: pointer;
}
@media (min-aspect-ratio: 375 / 668) {
  .cross {
    width: calc( (23 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .cross {
    width: calc( (23 / 900) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-aspect-ratio: 375 / 668) {
  .cross {
    height: calc( (23 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .cross {
    height: calc( (23 / 900) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-aspect-ratio: 375 / 668) {
  .cross {
    top: calc( (-23/2 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .cross {
    top: calc( (-23/2 / 900) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-aspect-ratio: 375 / 668) {
  .cross {
    right: calc( (-23/2 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .cross {
    right: calc( (-23/2 / 900) * (var(--vh, 1vh) * 100) );
  }
}
.cross .crossBg {
  border-radius: 100%;
  background-color: #fe3457;
  transform: scale(1);
  transition: transform 100ms ease-out;
  width: 100%;
  height: 100%;
  pointer-events: none;
}
.cross svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 2.66666667vw;
  height: 2.66666667vw;
}
@media (min-aspect-ratio: 375 / 668) {
  .cross svg {
    width: calc( (10 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .cross svg {
    width: calc( (10 / 900) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-aspect-ratio: 375 / 668) {
  .cross svg {
    height: calc( (10 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .cross svg {
    height: calc( (10 / 900) * (var(--vh, 1vh) * 100) );
  }
}
.cross:hover .crossBg {
  transform: scale(1.4);
}
