.root {
  z-index: 100;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.9);
}
.title {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 8vw;
  color: #fff;
}
@media (min-aspect-ratio: 375 / 668) {
  .title {
    font-size: calc( (30 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .title {
    font-size: calc( (30 / 900) * (var(--vh, 1vh) * 100) );
  }
}
