.root {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
@media (min-width: 1024px) {
  .infos {
    position: absolute;
    margin-left: -85.33333333vw;
    width: 80vw;
    text-align: right;
  }
}
@media (min-width: 1024px) and (min-aspect-ratio: 375 / 668) {
  .infos {
    margin-left: calc( (-320 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 1024px) and (min-width: 768px) {
  .infos {
    margin-left: calc( (-320 / 900) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 1024px) and (min-aspect-ratio: 375 / 668) {
  .infos {
    width: calc( (300 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 1024px) and (min-width: 768px) {
  .infos {
    width: calc( (300 / 900) * (var(--vh, 1vh) * 100) );
  }
}
.rank {
  color: #fe3457;
  font-size: 3.73333333vw;
}
@media (min-aspect-ratio: 375 / 668) {
  .rank {
    font-size: calc( (14 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .rank {
    font-size: calc( (18 / 900) * (var(--vh, 1vh) * 100) );
  }
}
.filename {
  margin-top: 0.53333333vw;
  font-size: 3.73333333vw;
  color: #b5b5b5;
}
@media (min-aspect-ratio: 375 / 668) {
  .filename {
    margin-top: calc( (2 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .filename {
    margin-top: calc( (2 / 900) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-aspect-ratio: 375 / 668) {
  .filename {
    font-size: calc( (14 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .filename {
    font-size: calc( (18 / 900) * (var(--vh, 1vh) * 100) );
  }
}
.wrapper {
  margin-top: 2.66666667vw;
  position: relative;
}
@media (min-aspect-ratio: 375 / 668) {
  .wrapper {
    margin-top: calc( (10 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .wrapper {
    margin-top: calc( (10 / 900) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 1024px) {
  .wrapper {
    margin-top: 0vw;
  }
}
@media (min-width: 1024px) and (min-aspect-ratio: 375 / 668) {
  .wrapper {
    margin-top: calc( (0 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 1024px) and (min-width: 768px) {
  .wrapper {
    margin-top: calc( (0 / 900) * (var(--vh, 1vh) * 100) );
  }
}
.image {
  border-radius: 0.2em;
  position: relative;
  width: 100%;
}
.canvas {
  position: absolute;
  top: 0;
}
.blurZoneBuilder {
  top: 0;
  width: 100%;
  height: 100%;
  position: absolute;
}
