.root {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.wrapper {
  position: relative;
}
.wrapper:hover .container_rollover {
  clip-path: polygon(100% 0, 100% 0, 100% 100%, 100% 100%);
}
.container {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 16vw;
  background-color: #fff;
  height: 13.06666667vw;
  padding-left: 5.97333333vw;
  padding-right: 5.97333333vw;
  padding-top: 5.97333333vw;
  padding-bottom: 5.97333333vw;
}
@media (min-aspect-ratio: 375 / 668) {
  .container {
    border-radius: calc( (60 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .container {
    border-radius: calc( (60 / 900) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-aspect-ratio: 375 / 668) {
  .container {
    height: calc( (49 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .container {
    height: calc( (56 / 900) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-aspect-ratio: 375 / 668) {
  .container {
    padding-left: calc( (22.4 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .container {
    padding-left: calc( (32.199999999999996 / 900) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-aspect-ratio: 375 / 668) {
  .container {
    padding-right: calc( (22.4 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .container {
    padding-right: calc( (32.199999999999996 / 900) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-aspect-ratio: 375 / 668) {
  .container {
    padding-top: calc( (22.4 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .container {
    padding-top: calc( (32.199999999999996 / 900) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-aspect-ratio: 375 / 668) {
  .container {
    padding-bottom: calc( (22.4 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .container {
    padding-bottom: calc( (32.199999999999996 / 900) * (var(--vh, 1vh) * 100) );
  }
}
.container_rollover.container {
  z-index: 1;
  position: absolute;
  left: 0;
  transition: clip-path 0.5s ease;
  clip-path: polygon(-10% 0, 100% 0, 100% 100%, -40% 100%);
  background-color: #fe3457;
}
.container_rollover.container .label {
  color: #fff;
}
.label {
  font-size: 4.85333333vw;
  color: #fe3457;
}
@media (min-aspect-ratio: 375 / 668) {
  .label {
    font-size: calc( (18.2 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .label {
    font-size: calc( (21 / 900) * (var(--vh, 1vh) * 100) );
  }
}
