.root {
  cursor: pointer;
  background-color: #283e63;
  padding: 2.66666667vw;
  border-radius: 10px;
}
@media (min-aspect-ratio: 375 / 668) {
  .root {
    padding: calc( (10 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .root {
    padding: calc( (40 / 900) * (var(--vh, 1vh) * 100) );
  }
}
.root:active {
  opacity: 0.8;
  text-decoration: underline;
}
.root:hover .svg path {
  fill: #fff;
}
.root:hover .label {
  color: #fff;
}
.svg {
  transition: all ease-out 200ms;
  width: 5.33333333vw;
}
@media (min-aspect-ratio: 375 / 668) {
  .svg {
    width: calc( (20 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .svg {
    width: calc( (40 / 900) * (var(--vh, 1vh) * 100) );
  }
}
.svg path {
  transition: all ease-out 200ms;
  fill: #fe3457;
}
.label {
  display: none;
}
@media (min-width: 768px) {
  .label {
    display: block;
    margin-top: 2.66666667vw;
    color: #fff;
    transition: all ease-out 200ms;
    font-size: 4.8vw;
    line-height: 1.6em;
  }
}
@media (min-width: 768px) and (min-aspect-ratio: 375 / 668) {
  .label {
    margin-top: calc( (10 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) and (min-width: 768px) {
  .label {
    margin-top: calc( (10 / 900) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) and (min-aspect-ratio: 375 / 668) {
  .label {
    font-size: calc( (18 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) and (min-width: 768px) {
  .label {
    font-size: calc( (18 / 900) * (var(--vh, 1vh) * 100) );
  }
}
