.root {
  cursor: pointer;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  font-size: 3.46666667vw;
  transition: all ease-out 200ms;
}
@media (min-aspect-ratio: 375 / 668) {
  .root {
    font-size: calc( (13 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .root {
    font-size: calc( (18 / 900) * (var(--vh, 1vh) * 100) );
  }
}
.root:active {
  opacity: 0.8;
  text-decoration: underline;
}
.root:hover {
  color: #fe3457;
}
.root:hover svg path {
  fill: #fff;
}
.label {
  margin-top: -1.06666667vw;
  margin-left: 1.86666667vw;
}
@media (min-aspect-ratio: 375 / 668) {
  .label {
    margin-top: calc( (-4 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .label {
    margin-top: calc( (-4 / 900) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-aspect-ratio: 375 / 668) {
  .label {
    margin-left: calc( (7 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .label {
    margin-left: calc( (10 / 900) * (var(--vh, 1vh) * 100) );
  }
}
