.root {
  padding-bottom: 13.33333333vw;
  position: relative;
  background: #0e203e;
}
@media (min-aspect-ratio: 375 / 668) {
  .root {
    padding-bottom: calc( (50 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .root {
    padding-bottom: calc( (50 / 900) * (var(--vh, 1vh) * 100) );
  }
}
.header {
  top: 0;
  left: 0;
  right: 0;
  position: fixed;
  height: 21.33333333vw;
  width: 100%;
  background: #0e203e;
  z-index: 1;
}
@media (min-aspect-ratio: 375 / 668) {
  .header {
    height: calc( (80 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .header {
    height: calc( (120 / 900) * (var(--vh, 1vh) * 100) );
  }
}
.headerBackground {
  position: absolute;
  top: 0;
  width: 100%;
  height: 34.66666667vw;
  background: #0e203e;
}
@media (min-aspect-ratio: 375 / 668) {
  .headerBackground {
    height: calc( (130 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .headerBackground {
    height: calc( (300 / 900) * (var(--vh, 1vh) * 100) );
  }
}
.restartButton {
  position: absolute;
  top: 50%;
  left: auto;
  transform: translateY(-50%);
  left: 3.46666667vw;
}
@media (min-aspect-ratio: 375 / 668) {
  .restartButton {
    left: calc( (13 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .restartButton {
    left: calc( (30 / 900) * (var(--vh, 1vh) * 100) );
  }
}
.logo {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transform: translate(-50%, -50%) scale(0.5);
}
.downloadButton {
  position: absolute;
  top: 50%;
  left: auto;
  transform: translateY(-50%);
  right: 5.33333333vw;
}
@media (min-aspect-ratio: 375 / 668) {
  .downloadButton {
    right: calc( (20 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .downloadButton {
    right: calc( (30 / 900) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 1024px) {
  .downloadButton {
    position: fixed;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: none;
    left: initial;
    top: 8vw;
    width: 66.66666667vw;
  }
}
@media (min-width: 1024px) and (min-aspect-ratio: 375 / 668) {
  .downloadButton {
    top: calc( (30 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 1024px) and (min-width: 768px) {
  .downloadButton {
    top: calc( (30 / 900) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 1024px) and (min-aspect-ratio: 375 / 668) {
  .downloadButton {
    width: calc( (250 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 1024px) and (min-width: 768px) {
  .downloadButton {
    width: calc( (250 / 900) * (var(--vh, 1vh) * 100) );
  }
}
.list {
  margin-top: 29.33333333vw;
  margin: auto;
  width: 80%;
}
@media (min-aspect-ratio: 375 / 668) {
  .list {
    margin-top: calc( (110 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .list {
    margin-top: calc( (130 / 900) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 1024px) {
  .list {
    width: 217.6vw;
    margin-left: auto;
    margin-right: auto;
  }
}
@media (min-width: 1024px) and (min-aspect-ratio: 375 / 668) {
  .list {
    width: calc( (816 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 1024px) and (min-width: 768px) {
  .list {
    width: calc( (816 / 900) * (var(--vh, 1vh) * 100) );
  }
}
.item {
  margin-top: 13.33333333vw;
  position: relative;
}
@media (min-aspect-ratio: 375 / 668) {
  .item {
    margin-top: calc( (50 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .item {
    margin-top: calc( (50 / 900) * (var(--vh, 1vh) * 100) );
  }
}
