.root {
  display: block;
}
.loading {
  font-family: "sacramento-regular", sans-serif;
  font-size: 13.33333333vw;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #fff;
  animation: blink 2s linear infinite;
}
@media (min-aspect-ratio: 375 / 668) {
  .loading {
    font-size: calc( (50 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .loading {
    font-size: calc( (120 / 900) * (var(--vh, 1vh) * 100) );
  }
}
@keyframes blink {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}
