.wrapper {
  position: relative;
}
.card {
  position: relative;
  background: #e5e5e5;
  width: 66.66666667vw;
  height: 88.88888889vw;
}
@media (min-aspect-ratio: 375 / 668) {
  .card {
    width: calc( (250 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .card {
    width: calc( (250 / 900) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-aspect-ratio: 375 / 668) {
  .card {
    height: calc( (250 / 0.75 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .card {
    height: calc( (250 / 0.75 / 900) * (var(--vh, 1vh) * 100) );
  }
}
.pic {
  position: absolute;
  top: auto;
  left: 50%;
  transform: translateX(-50%);
  top: 7.46666667vw;
  background: #b5b5b5;
  width: 53.33333333vw;
  height: 65.04065041vw;
}
@media (min-aspect-ratio: 375 / 668) {
  .pic {
    top: calc( (28 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .pic {
    top: calc( (28 / 900) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-aspect-ratio: 375 / 668) {
  .pic {
    width: calc( (200 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .pic {
    width: calc( (200 / 900) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-aspect-ratio: 375 / 668) {
  .pic {
    height: calc( (200 / 0.82 / 667) * (var(--vh, 1vh) * 100) );
  }
}
@media (min-width: 768px) {
  .pic {
    height: calc( (200 / 0.82 / 900) * (var(--vh, 1vh) * 100) );
  }
}
.deco {
  width: 10.66666667vw;
  height: 2px;
  background-color: #b5b5b5;
  position: absolute;
  bottom: 13.33333333vw;
  right: 6.66666667vw;
}
@media (min-aspect-ratio: 375 / 668) {
